import React, { useState } from "react";
import { addVolunteer } from "../services/volunteerservices";


// Define the type for props
interface AddVolunteerProps {
    refreshData: () => void; // Function with no parameters that returns void
  }
  
  const AddVolunteer: React.FC<AddVolunteerProps> = ({ refreshData }) => {
    const [newVolunteer, setNewVolunteer] = useState({ name: "", email: "", status: "Pending" });
  
    const handleAddVolunteer = async () => {
      await addVolunteer(newVolunteer);
      refreshData(); // Refresh volunteers list after adding
    };
  
    return (
      <section className="bg-white p-4 rounded shadow">
        <h2 className="text-xl font-semibold mb-4">Add Volunteer</h2>
        <input
          type="text"
          placeholder="Name"
          className="border p-2 mb-2 w-full"
          value={newVolunteer.name}
          onChange={(e) => setNewVolunteer({ ...newVolunteer, name: e.target.value })}
        />
        <input
          type="email"
          placeholder="Email"
          className="border p-2 mb-2 w-full"
          value={newVolunteer.email}
          onChange={(e) => setNewVolunteer({ ...newVolunteer, email: e.target.value })}
        />
        <button className="bg-green-600 text-white px-4 py-2 rounded" onClick={handleAddVolunteer}>
          Add Volunteer
        </button>
      </section>
    );
  };
  
  export default AddVolunteer;