'use client';

import React, { useEffect, useState } from "react";
import SideBar from "../../../components/layout/SideBar";
import { Bell } from "lucide-react";
import VolunteerOverview from "../VolunteerManage/volunteers/VolunteerOverview";
import VolunteerTable from "../VolunteerManage/volunteers/VolunteerTable";
import AddVolunteer from "../VolunteerManage/volunteers/AddVolunteers";
import { FiBell } from "react-icons/fi";
//import { getVolunteers, deleteVolunteer } from "../../services/volunteerService";

// Define the Volunteer type
interface Volunteer {
  id: string;
  name: string;
  email: string;
  phone: string;
  status: string;
  points: number;
  tasks: string[];
}

const VolunteerManagement = () => {
  const [volunteers, setVolunteers] = useState<Volunteer[]>([
    { id: "1", name: "John Doe", email: "john@example.com", phone: "123-456-7890", status: "Pending", points: 0, tasks: [] },
    { id: "2", name: "Jane Smith", email: "jane@example.com", phone: "987-654-3210", status: "Active", points: 10, tasks: ["Task 1"] }
  ]);
  
  const [search, setSearch] = useState("");

  // Mock function for fetching volunteers (simulating a DB call)
  const fetchVolunteers = async () => {
    // Simulating an API delay
    await new Promise((resolve) => setTimeout(resolve, 500));
    setVolunteers([
      { id: "1", name: "John Doe", email: "john@example.com", phone: "123-456-7890", status: "Pending", points: 0, tasks: [] },
      { id: "2", name: "Jane Smith", email: "jane@example.com", phone: "987-654-3210", status: "Active", points: 10, tasks: ["Task 1"] }
    ]);
  };

  useEffect(() => {
    fetchVolunteers();
  }, []);

  // Mock delete function
  const handleDelete = async (id: string) => {
    setVolunteers((prev) => prev.filter((v) => v.id !== id));
  };

  return (
    <div className="min-h-screen bg-gray-100 flex">
      <SideBar />
      <div className="flex-1 p-6">
        <header className="bg-white shadow p-4 flex justify-between items-center mb-6">
          <input
            type="search"
            placeholder="Search..."
            className="border p-2 rounded w-64"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />
          <FiBell className="w-6 h-6 text-gray-600" />
        </header>

        <VolunteerOverview volunteers={volunteers} />
        <VolunteerTable volunteers={volunteers} handleDelete={handleDelete} />
        <AddVolunteer refreshData={fetchVolunteers} />
      </div>
    </div>
  );
};

export default VolunteerManagement;
